/* eslint-disable jsx-a11y/label-has-associated-control */
import { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';

import Loader from '../loader/loader';

import { changeNameAction, changeEmailAction } from '../../store/actions/auth';
import { changePassword } from '../../services/auth.service';
import { getPremiumDate } from '../../services/users.service';

import Payment from '../payment/payment';

import './profileComponent.css';

const ANYMONEY_MERCHANT = '9240';
const ANYMONEY_KEY = '_g7Sn_ki4ow5SXfDyHQZhjlQAj3NiFvO0W0O10zVBoYYT8x7OMM8kMeSjOjxsSS__VIx';

function ExpiresOn(props) {
  const { premium, level } = props;
  if (premium) {
    return (
      <h4 className="left-column-h">
        Expires on -
        {' '}
        { }
        <span className={level !== 'basic' ? 'level-premium' : 'level-basic'}>{premium}</span>
      </h4>
    );
  }
  return (
    <h4 className="left-column-h">
      <Loader />
    </h4>
  );
}

function ProfileComponent() {
  const {
    email, name, isLoggedIn, level, token,
  } = useSelector((state) => state.auth);
  const [userEmail, setUserEmail] = useState(email);
  const [userName, setUserName] = useState(name);
  const [pass1, setPass1] = useState('');
  const [pass2, setPass2] = useState('');
  const [emailMsg, setEmailMsg] = useState({});
  const [nameMsg, setNameMsg] = useState({});
  const [passMsg, setPassMsg] = useState({});
  const [premium, setPremium] = useState(null);
  const [sign, setSign] = useState('');
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const isValidEmail = (validationStr) => (/\S+@\S+\.\S+/.test(validationStr));

  const payData = {
    merchant: ANYMONEY_MERCHANT,
    amount: '20',
    in_curr: 'EUR',
    externalid: '123',
    expiry: '600',
    client_email: email,
    callback_url: 'https://example.com/order_handler',
    redirect_url: 'https://example.com/order_page/',
  };

  const sha512 = async (data, key) => {
    const objKeys = [];
    // eslint-disable-next-line no-restricted-syntax, guard-for-in
    for (const k in data) {
      objKeys.push(k);
    }
    objKeys.sort();
    let str = '';
    for (let i = 0; i < objKeys.length; i += 1) {
      str += data[objKeys[i]];
    }

    const enc = new TextEncoder('utf-8');
    const _key = await window.crypto.subtle.importKey(
      'raw', // raw format of the key - should be Uint8Array
      enc.encode(key),
      { // algorithm details
        name: 'HMAC',
        hash: 'SHA-512',
      },
      false, // export = false
      ['sign', 'verify'], // what this key can do
    );
    const signature = await window.crypto.subtle.sign(
      'HMAC',
      _key,
      enc.encode(str.toLowerCase()),
    );
    const b = new Uint8Array(signature);
    return Array.prototype.map.call(b, (x) => x.toString(16).padStart(2, '0')).join('');
  };

  useEffect(() => {
    if (!isLoggedIn) {
      navigate('/');
    }
    const fetchData = async () => {
      if (level === 'premium') {
        try {
          const res = await getPremiumDate(token);
          if (res.status) {
            setPremium(res.premium_expired);
          }
        } catch (err) {
          console.error(err);
        }
      }
      const digest = await sha512(payData, ANYMONEY_KEY);
      console.log(digest);
      setSign(digest);
    };
    fetchData();
  }, [isLoggedIn, level, navigate, token]);

  const onChangeName = (e) => {
    setUserName(e.target.value);
  };

  const onChangeEmail = (e) => {
    setUserEmail(e.target.value);
  };

  const onChangePass1 = (e) => {
    setPass1(e.target.value);
  };

  const onChangePass2 = (e) => {
    setPass2(e.target.value);
  };

  const onSubmitEmail = async (e) => {
    e.preventDefault();
    if (isValidEmail(userEmail)) {
      const res = await dispatch(changeEmailAction(
        userEmail,
        token,
      ));
      if (res.error) {
        setEmailMsg({
          style: 'error',
          text: res.msg,
        });
        return;
      }
      setEmailMsg({
        style: 'success',
        text: res.msg,
      });
      return;
    }
    setEmailMsg({
      style: 'error',
      text: 'Email not valid',
    });
  };

  const onSubmitName = async (e) => {
    e.preventDefault();
    try {
      await dispatch(changeNameAction(
        userName,
        token,
      ));
      setNameMsg({
        style: 'success',
        text: 'Name changed',
      });
    } catch (err) {
      setNameMsg({
        style: 'error',
        text: err.message,
      });
    }
  };

  const onSubmitPassword = async (e) => {
    e.preventDefault();
    if (pass1 === pass2) {
      if (pass1.length < 6) {
        setPassMsg({
          style: 'error',
          text: 'password must be at least 6 characters long',
        });
        return;
      }
      try {
        const res = await changePassword(pass1, token);
        setPassMsg({
          style: 'success',
          text: res.msg,
        });
        setPass1('');
        setPass2('');
        return;
      } catch (err) {
        setPassMsg({
          style: 'error',
          text: err.message,
        });
        return;
      }
    }
    setPassMsg({
      style: 'error',
      text: 'Passwords is not identical',
    });
    setPass1('');
    setPass2('');
  };

  const passDisabled = !(!!pass1 && pass1 === pass2);
  const nameDisabled = !userName;
  const emailDisabled = !userEmail;

  return (
    <>
      <div className="profile-wrapper">
        <div className="profile-left">
          <img className="profile-image-center" src="/user-profile-image.svg" alt="user profile template" />
          <h4 className="left-column-h">{name}</h4>
          <h4 className="left-column-h">
            Account -
            <span className={level !== 'basic' ? 'level-premium' : 'level-basic'}>{level}</span>
          </h4>
          {level === 'premium' ? <ExpiresOn premium={premium} level={level} /> : null}
        </div>

        <div className="profile-right">
          <h2>Credentials</h2>
          <hr />
          <form className="form-inline" onSubmit={onSubmitEmail}>
            <label htmlFor="email">Email:</label>
            <input type="text" id="email" name="Email" value={userEmail} onChange={onChangeEmail} />
            <button type="submit" disabled={emailDisabled}>Change email</button>
          </form>
          {emailMsg.text ? <p className={`form-message-${emailMsg.style}`}>{emailMsg.text}</p> : null}
          <form className="form-inline" onSubmit={onSubmitName}>
            <label htmlFor="name">Name:</label>
            <input type="text" id="name" name="Name" value={userName} onChange={onChangeName} />
            <button type="submit" disabled={nameDisabled}>Change name</button>
          </form>
          {nameMsg.text ? <p className={`form-message-${nameMsg.style}`}>{nameMsg.text}</p> : null}
          <form className="form-inline" onSubmit={onSubmitPassword}>
            <label htmlFor="name">Password:</label>
            <input type="password" id="password" value={pass1} name="password" onChange={onChangePass1} />
            <input type="password" id="password-repeat" value={pass2} name="password" onChange={onChangePass2} />
            <button type="submit" disabled={passDisabled}>Change password</button>
          </form>
          {passMsg.text ? <p className={`form-message-${passMsg.style}`}>{passMsg.text}</p> : null}
        </div>
      </div>
      <form
        name="payment"
        method="post"
        action="https://sci.any.money/invoice"
        acceptCharset="UTF-8"
      >
        <input type="hidden" name="sign" value={sign} />
        <input type="hidden" name="merchant" value={ANYMONEY_MERCHANT} />
        <input type="hidden" name="amount" value={payData.amount} />
        <input type="hidden" name="in_curr" value={payData.in_curr} />
        <input type="hidden" name="externalid" value={payData.externalid} />
        <input type="hidden" name="expiry" value={payData.expiry} />
        <input type="hidden" name="client_email" value={payData.email} />
        <input type="hidden" name="callback_url" value="https://example.com/order_handler" />
        <input type="hidden" name="redirect_url" value="https://example.com/order_page/" />
        <input type="submit" color="secondary" className="w-100 btn btn-primary" value="Continue to checkout" />
      </form>
      <Payment />
    </>
  );
}

export default ProfileComponent;
